<template>
  <canvas ref="lineChart"></canvas>
</template>

<script>
// import { Line } from 'vue-chartjs';
import { Chart as ChartJS, Title, Tooltip, Legend, ArcElement, LineController, LineElement, PointElement, CategoryScale, LinearScale } from 'chart.js';
// import { Chart, ChartConfiguration, LineController, LineElement, PointElement, LinearScale, Title} from 'chart.js'

ChartJS.register(Title, Tooltip, Legend, ArcElement, LineController, LineElement, PointElement, CategoryScale, LinearScale);
// Chart.register(LineController, LineElement, PointElement, LinearScale, Title);


let myChart;
export default {
  // components: {
  //   Line,
  // },
  props: {
    chartData: Object,
    // chartOptions: Object,
    width: Number, 
    height: Number,
    // width: {
    //   type: Number,
    //   default: 200
    // }, 
    // height: {
    //   type: Number,
    //   default: 200
    // }
  },
  mounted() {
      this.renderChart();
  }, 
  methods: {
    renderChart() {
      var chartOptions = {
                                scales: {
                                  x: {
                                    grid: {
                                      display: true
                                    },
                                    ticks: {
                                      padding: 1
                                    }
                                  },
                                  y: {
                                    grid: {
                                      drawBorder: false,
                                      color: "lightgray",
                                      lineWidth: 1
                                    },
                                    min: -1,
                                    max: 1,
                                    // ticks: {
                                    //   stepSize: 100,
                                    //   padding: 10,
                                    // }
                                  }
                                },
                                maintainAspectRatio: true,   //  
                              }

      myChart = new ChartJS(this.$refs.lineChart, 
                            { 
                              type: 'line', 
                              data: this.chartData, 
                              // options: this.chartOptions,
                              options: chartOptions,
                              width: this.width,
                              height: this.height,
                              position: 'relative'
                            });
    },
    updateChart() {
      myChart.destroy();
      this.renderChart();
      // myChart.update();
    },
  },
  updated() {
    this.updateChart();
  },
}
</script>
